.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 10%;
    color: #ddd;
    border-bottom: 2px solid #3c3c3c;
}
.navbar .logo{
    width: max(12vw,120px);
}
.navbar ul{
    display: flex;
    list-style: none;
    gap: 40px;
}
.navbar li{
    cursor: pointer;
}
.navright{
    display: flex;
    align-items: center;
    gap: max(1vw,12px);
}
.navbar select{
    padding: 5px 8px;
    border-radius: 6px;
    border: 2px solid white;
    background: transparent;
    color: white;
}
.navbar  option{
    background-color: #000428;
    color: white;
}
.navbar button{
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px 25px;
    border-radius: 20px;
    font-size: 15px;
    font-weight: 500;
    background-color: white;
    color: #000428;
    border: none;
    cursor: pointer;
}
.navbar button img{
    width: 10px;
}
@media (max-width:900px) {
    .navbar{
        padding: 20px 8%;
    }
    .navbar button{
        gap: 8px;
        padding: 8px 18px;
        font-size: 14px;
    }
}
@media (max-width:768px) {
    .navbar{
        padding: 15px 2%;
    }
    .navbar ul{
        display: none;
    }
    .navbar button img{
        width: 10px;
    }
}